.toolbar{
	position: fixed;
	width:100%;
	background: rgba(0,0,0,0.8);
	height:56px;
	top:0;
	left:0;
	z-index: 3;

}

.toolbar__navigation {
	height:100%;
	display:flex;
	flex-direction: row;
	align-items: center;
	padding:0 1rem;

}

.toolbar-title{
	display:flex;
	color: #ed146f;
	font-size: 2rem;
  font-weight:300;
  margin-left:15px;
  align-self:center;
  
}
.toolbar-logo{
	display: flex;
	align-self:center;
}

@media (min-width: 769px){
	.toolbar__navigation{
		display:none;
	}
	.toolbar{
		display:none;
	}
}